.section {
  &-hero {
    &__wrapper {
      height: calc(100vh - 130px);
      min-height: 700px;

      position: relative;

      display: flex;
      align-items: center;
      justify-content: center;

      &::before{
        content: '';
        position: absolute;

        width: 100%;
        height: 100%;
        display: block;

        background-color: rgba( #272525,.3);
        z-index: 2;
      }
    }
  }
}

.hero {
  &-bg {
    &-slider {
      position: absolute;

      width: 100%;
      height: 100%;

      z-index: 1;

      &__item {
        height: 100%;
        width: 100%;
      }
      &__img{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
  }
  &-slider{
    position: relative;
    z-index: 3;
    &__item{
      width: 507px;
      border: solid 1px #fff;

      padding: 33px 0 25px 0;

      &-status{
        position: relative;

        display: flex;
        align-items: center;
        justify-content: space-between;

        font-size: 25px;
        font-weight: 700;
        text-transform: uppercase;
        color: #be983e;
        &::before,&::after{
          content: '';
          width: 160px;
          height: 1px;
          display: block;
          background-color: #be983e;
        }
      }
      &-title{
        margin-top: 30px;

        font-size: 30px;
        font-weight: 700;
        color:#fff;
        text-align: center;
      }
      &-address{
        font-size: 20px;
        color:#fff;
        text-align: center;
      }
    }
    &__img{
      margin-top: 20px;

      position: relative;

      width: auto;
      height: 275px;

      display: flex;
      align-items: center;
      justify-content: center;
      &-item{
        position: absolute;
        top: 0;
        height: 100%;
      }
    }

    &__ready{
      margin-top: 20px;
      &-row{
        position: relative;

        max-width: 387px;
        height: 15px;
        width: 100%;

        margin: 0 auto;

        background-color: rgba(#be983e,.55);
      }
      &-completed{
        position: absolute;
        top: 0;
        left: 0;

        height: 100%;

        background-image: linear-gradient(to top, #be983e, #dfbe5f, #be983e);
      }
      &-text{
        margin-top: 20px;

        text-align: center;

        font-size: 16px;
        color:#fff;
      }
    }
  }
  &-pagination{
    display: flex;
    align-items: center;
    justify-content: center;

    margin-top: 35px;
    .swiper{
      &-pagination{
        &-bullet{
          opacity: 1;
          background-color: rgba(#f8f8f8,.5);
          &-active{
            background-color: #f8f8f8;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1440px) {
  .section{
    &-hero{
      &__wrapper{
        height: auto;
        padding: 30px 0;
      }
    }
  }
}


@media screen and (max-width: 650px) {
  .hero{
    &-slider{
      &__item{
        width: 90%;
        &-status{
          &::before,&::after{
            width: 25%;
          }
        }
      }
      &__ready{
        padding: 0 15px;
      }
    }
  }
}