.section{
  &-layout{
    .section__header + &__content{
      margin-top: 30px;
    }
    &__content{
      height: 100%;
    }
  }
}
.layout{
  &-category{
    display: flex;
    flex-direction: column;

    height: 100%;
  }
  &-slider{
    height: 606px;
    &__link{
      display: flex;
      flex-direction: column;

      border: solid 1px #ededed;

      transition: .3s border-color;
    }
    &__title{
      height: 35px;
      padding: 0 15px;

      display: flex;
      align-items: center;

      background-color: #ededed;

      font-size: 14px;
      font-weight: 500;

      color:#262626;

      transition: .3s background-color, .3s color;
    }
    &__item{
      &.active{
        .layout{
          &-slider{
            &__link{
              border-color: #be983e;
            }
            &__title{
              background-image: linear-gradient(to top, #be983e, #dfbe5f, #be983e);
              color:#fff;
            }
          }
        }
      }
    }
    &__img{
      height: 165px;
      padding: 15px;

      display: flex;
      align-items: center;
      justify-content: center;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &-button{
        position: absolute;
        width: 30px;
        height: 15px;

        right: unset;
        left: 50%;
        margin-left: -15px;
        top: unset;

        bottom: -30px;

        background: url("../img/layout-arrow.png") center center no-repeat;
        background-size: contain;
    }
  }
  &-tabs{
    &__wrapper{
      display: grid;
      grid-template-columns: 300px auto;
      gap: 35px;

      grid-auto-rows: 606px;
    }

    &__block{
      display: flex;
      flex-direction: column;
    }
  }
  &-category{
    &__nav{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &-nav{
      width: 100%;
      padding-bottom: 3px;
    }
    &__item{
      flex-shrink: 0;
      &.active{
        .layout{
          &-category{
            &__link{
              text-decoration-color: transparent;
              color:#be983e;
            }
          }
        }
      }
    }
    &__link{
      font-size: 16px;
      color:#262626;

      text-decoration: underline;
      text-underline-position: under;
      text-decoration-color: #be983e;

      transition: .3s color, .3s decoration-color;
    }
    &__content{
      margin-top: 40px;
      height: 100%;
    }
  }
  &-item{
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    gap: 80px;
    height: 100%;
    &__block{
      display: flex;
      align-items: center;
    }
    &__img{
      width: 100%;
      height: 100%;
      &-item{
        object-fit: contain;
      }
    }
    &__wrapper{
      display: flex;
      flex-direction: column;
      height: 100%;
    }
    &__category{
      height: 35px;
      padding: 0 15px;

      display: flex;
      align-items: center;
      justify-content: flex-start;

      background-image: linear-gradient(to top, #be983e, #dfbe5f, #be983e);

      font-size: 14px;
      font-weight: 500;
      color:#fefefe;
    }
    &__title{
      margin-top: 35px;

      font-size: 20px;
      line-height: 30px;
      font-weight: 700;
      color:#262626;
    }
    &__address{
      margin-top: 30px;
      width: max-content;
      &-label{
        font-size: 14px;
        color:#535352;
      }
      &-links{
        display: flex;
        align-items: center;
        margin-top: 5px;
      }
      &-link + &-link{
        margin-left: 10px;
      }
      &-link{
        font-size: 12px;
        color:#848484;
        text-decoration: underline;
        text-underline-position: under;
      }
    }
    &__content{
      padding: 15px 0;
      border-top: 1px solid #ededed;
      border-bottom: 1px solid #ededed;

      margin-top: auto;
    }
    &__desc{
      font-size: 13px;
      line-height: 20px;
      color:#262626;
    }
    &__footer{
      margin-top: 25px;
    }
    &__price{
      font-size: 24px;
      font-weight: 700;
      color:#262626;
    }
    &__btn{
      margin-top: 25px;

      display: flex;
      align-items: center;
      justify-content: center;

      width: 230px;
      height: 45px;

      background-color: #be983e;

      font-size: 14px;
      font-weight: 700;
      color:#fefefe;
    }
  }
}

@media screen and (max-width: 991px) {
  .layout{
    &-tabs{
      &__wrapper{
        grid-template-columns: minmax(0,1fr);
        gap: 15px;
        grid-auto-rows: auto;
      }
    }
    &-slider{
      height: 202px;
      &-button{
        left: unset;
        right: -15px;
        top: 50%;
        bottom: unset;
        margin-top: -7.5px;
        transform: rotate(-90deg);
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .layout{
    &-item{
      grid-template-columns: minmax(0,1fr);
      gap: 15px;
      &__content{
        margin-top: 15px;
      }
      &__img{
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
    &-slider{
      &__item{
        width: 300px;
      }
    }
    &-category{
      &__nav{
        align-items: flex-start;
        flex-direction: column;
      }
      &__item + &__item{
        margin-top: 10px;
      }
    }
  }
}