*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Bold/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold/Montserrat-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Medium/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Regular/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular/Montserrat-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
}

.container {
  max-width: 1235px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.header__wrapper {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding: 6px 0 12px 40px;
  height: 130px;
}

.header__block {
  padding-bottom: 20px;
}

.header__block-logo {
  padding-bottom: 0;
}

.header__block-burger {
  display: none;
}

.header__logo {
  height: 110px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__logo-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__link {
  font-size: 12px;
  color: #575756;
  text-transform: uppercase;
}

.header__phone {
  font-size: 18px;
  font-weight: 700;
  color: #575756;
  text-transform: uppercase;
}

.header-nav__list {
  display: flex;
  align-items: center;
}

.header-nav__item + .header-nav__item {
  margin-left: 40px;
}

.header-social__wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header-social__item + .header-social__item {
  margin-left: 15px;
}

.header-social__img {
  width: 25px;
  height: 20px;
  object-fit: contain;
}

.header-phone {
  margin-top: 20px;
  text-align: right;
}

.burger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  width: 30px;
  height: 20px;
  position: relative;
}

.burger__item {
  width: 100%;
  height: 2px;
  display: block;
  flex-shrink: 0;
  background-color: #000000;
  border-radius: 2px;
  transition: 0.5s all;
}

.burger.active .burger__item:nth-child(2) {
  display: none;
}

.burger.active .burger__item:nth-child(1) {
  position: absolute;
  left: 0;
  top: 0;
  transform: rotate(45deg);
}

.burger.active .burger__item:nth-child(3) {
  position: absolute;
  right: 0;
  top: 0;
  transform: rotate(-45deg);
}

.nav-item__link_mobile {
  display: none;
}

@media screen and (max-width: 991px) {
  .nav-item__link_mobile {
    display: block;
  }
  .header__block {
    display: none;
    padding-bottom: 0;
    flex: 33%;
  }
  .header__block-burger {
    display: block;
    z-index: 6;
  }
  .header__block-logo {
    display: block;
  }
  .header__block-social {
    display: block;
  }
  .header__block-nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 50%;
    height: 100vh;
    background-color: #fff;
    display: block;
    z-index: 5;
    padding-top: 80px;
    transform: translateX(-100%);
    opacity: 0;
    transition: 0.5s all linear;
  }
  .header__block-nav.active {
    opacity: 1;
    transform: translateX(0);
  }
  .header__wrapper {
    align-items: center;
    justify-content: space-between;
    padding: 6px 0;
    height: auto;
  }
  .header__logo {
    height: 40px;
  }
  .header-phone {
    margin-top: 5px;
  }
  .header-nav__list {
    flex-direction: column;
    align-items: flex-start;
    padding-left: 15px;
  }
  .header-nav__item + .header-nav__item {
    margin-left: 0;
    margin-top: 10px;
  }
}

@media screen and (max-width: 375px) {
  .header__phone {
    font-size: 10px;
  }
  .header-phone {
    margin-top: 0;
  }
  .header-social__img {
    width: 15px;
    height: 15px;
  }
}

.footer__wrapper {
  background-color: #363636;
  padding: 5px 0 30px 0;
}

.footer__row {
  display: flex;
  align-items: flex-start;
}

.footer__row + .footer__row {
  margin-top: 10px;
}

.footer__social {
  margin-left: auto;
  align-self: center;
}

.footer__phone {
  margin-left: 45px;
  align-self: center;
}

.footer__phone-label {
  font-size: 18px;
  font-weight: 700;
  color: #ffffff;
}

.footer__copyright-label {
  font-size: 12px;
  line-height: 22px;
  color: #fff;
}

.footer__nav {
  margin-left: auto;
}

.footer-nav__list {
  display: flex;
  align-items: center;
}

.footer-nav__item + .footer-nav__item {
  margin-left: 35px;
}

.footer-nav__link {
  font-size: 12px;
  text-transform: uppercase;
  color: #ffffff;
  transition: .3s color;
}

.footer-nav__link:hover {
  color: #be983e;
}

.footer-social {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-social__item {
  width: 25px;
  height: 16px;
}

.footer-social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.footer-social__link svg {
  width: 100%;
  height: 100%;
  fill: #fff;
}

.footer-social__link path {
  fill: #fff;
  transition: .3s fill;
}

.footer-social__link:hover path {
  fill: #be983e;
}

@media screen and (max-width: 767px) {
  .footer__row {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .footer__social, .footer__phone {
    margin-left: 0;
  }
  .footer__phone {
    margin-top: 10px;
  }
  .footer__copyright-label {
    text-align: center;
  }
  .footer-nav__list {
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
  }
  .footer-nav__item + .footer-nav__item {
    margin: 0;
  }
  .footer-nav__item {
    margin: 5px 10px;
  }
}

.section__wrapper {
  padding: 75px 0;
}

.section__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.section__title {
  font-size: 24.5px;
  font-weight: 700;
  color: #262626;
}

.section__desc {
  font-size: 14px;
  line-height: 19px;
  color: #262626;
  text-align: justify;
}

.section__desc p + p {
  margin-top: 15px;
}

.section__link {
  font-size: 14px;
  color: #262626;
  text-decoration: underline;
  text-underline-position: under;
  text-decoration-color: #be983e;
}

.section__header + .section__content {
  margin-top: 44px;
}

@media screen and (max-width: 991px) {
  .section__wrapper {
    padding: 30px 0;
  }
}

@media screen and (max-width: 475px) {
  .section__title {
    font-size: 18px;
  }
  .section__header + .section__content {
    margin-top: 15px;
  }
}

.section-about {
  background-color: #f1f2f6;
}

.section-about__img {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-auto-rows: 230px;
  gap: 13px;
}

.section-about__img-item_row-big {
  grid-row-end: span 2;
}

.section-about__img-item_column-big {
  grid-column-end: span 2;
}

.section-about__img-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.section-about__grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 36px;
}

.section-about__content-block + .section-about__content-block {
  margin-top: 33px;
}

@media screen and (max-width: 800px) {
  .section-about__grid {
    grid-template-columns: minmax(0, 1fr);
    gap: 30px;
  }
}

.news-slide {
  width: calc(25% - 25px);
}

.news-slide_big {
  width: calc(50% - 50px);
}

.news-item__img {
  height: 368px;
}

.news-item__img-item {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.news-item__content {
  margin-top: 25px;
}

.news-item__time {
  font-size: 14px;
  font-weight: 500;
  color: #262626;
}

.news-item__time-label {
  font-size: 24px;
  font-weight: 700;
  color: #be983e;
  margin-right: 5px;
}

.news-item__title {
  margin-top: 20px;
}

.news-item__title-label {
  font-size: 18px;
  font-weight: 700;
  color: #262626;
}

.news-item__desc {
  margin-top: 30px;
  font-size: 14px;
  line-height: 19px;
  color: #262626;
}

.news-item__footer {
  margin-top: 15px;
}

.news-item__link {
  font-size: 12px;
  color: #848484;
  text-decoration: underline;
  text-underline-position: under;
  transition: .3s color, .3s text-decoration;
}

.news-item__link:hover {
  color: #be983e;
  text-decoration-color: transparent;
}

@media screen and (max-width: 991px) {
  .news-slide {
    width: 100%;
  }
  .news-slide_big {
    width: 100%;
  }
}

.section__header + .section-layout__content {
  margin-top: 30px;
}

.section-layout__content {
  height: 100%;
}

.layout-category {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.layout-slider {
  height: 606px;
}

.layout-slider__link {
  display: flex;
  flex-direction: column;
  border: solid 1px #ededed;
  transition: .3s border-color;
}

.layout-slider__title {
  height: 35px;
  padding: 0 15px;
  display: flex;
  align-items: center;
  background-color: #ededed;
  font-size: 14px;
  font-weight: 500;
  color: #262626;
  transition: .3s background-color, .3s color;
}

.layout-slider__item.active .layout-slider__link {
  border-color: #be983e;
}

.layout-slider__item.active .layout-slider__title {
  background-image: linear-gradient(to top, #be983e, #dfbe5f, #be983e);
  color: #fff;
}

.layout-slider__img {
  height: 165px;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.layout-slider__img-item {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.layout-slider-button {
  position: absolute;
  width: 30px;
  height: 15px;
  right: unset;
  left: 50%;
  margin-left: -15px;
  top: unset;
  bottom: -30px;
  background: url("../img/layout-arrow.png") center center no-repeat;
  background-size: contain;
}

.layout-tabs__wrapper {
  display: grid;
  grid-template-columns: 300px auto;
  gap: 35px;
  grid-auto-rows: 606px;
}

.layout-tabs__block {
  display: flex;
  flex-direction: column;
}

.layout-category__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.layout-category-nav {
  width: 100%;
  padding-bottom: 3px;
}

.layout-category__item {
  flex-shrink: 0;
}

.layout-category__item.active .layout-category__link {
  text-decoration-color: transparent;
  color: #be983e;
}

.layout-category__link {
  font-size: 16px;
  color: #262626;
  text-decoration: underline;
  text-underline-position: under;
  text-decoration-color: #be983e;
  transition: .3s color, .3s decoration-color;
}

.layout-category__content {
  margin-top: 40px;
  height: 100%;
}

.layout-item {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 80px;
  height: 100%;
}

.layout-item__block {
  display: flex;
  align-items: center;
}

.layout-item__img {
  width: 100%;
  height: 100%;
}

.layout-item__img-item {
  object-fit: contain;
}

.layout-item__wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.layout-item__category {
  height: 35px;
  padding: 0 15px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-image: linear-gradient(to top, #be983e, #dfbe5f, #be983e);
  font-size: 14px;
  font-weight: 500;
  color: #fefefe;
}

.layout-item__title {
  margin-top: 35px;
  font-size: 20px;
  line-height: 30px;
  font-weight: 700;
  color: #262626;
}

.layout-item__address {
  margin-top: 30px;
  width: max-content;
}

.layout-item__address-label {
  font-size: 14px;
  color: #535352;
}

.layout-item__address-links {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.layout-item__address-link + .layout-item__address-link {
  margin-left: 10px;
}

.layout-item__address-link {
  font-size: 12px;
  color: #848484;
  text-decoration: underline;
  text-underline-position: under;
}

.layout-item__content {
  padding: 15px 0;
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;
  margin-top: auto;
}

.layout-item__desc {
  font-size: 13px;
  line-height: 20px;
  color: #262626;
}

.layout-item__footer {
  margin-top: 25px;
}

.layout-item__price {
  font-size: 24px;
  font-weight: 700;
  color: #262626;
}

.layout-item__btn {
  margin-top: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 230px;
  height: 45px;
  background-color: #be983e;
  font-size: 14px;
  font-weight: 700;
  color: #fefefe;
}

@media screen and (max-width: 991px) {
  .layout-tabs__wrapper {
    grid-template-columns: minmax(0, 1fr);
    gap: 15px;
    grid-auto-rows: auto;
  }
  .layout-slider {
    height: 202px;
  }
  .layout-slider-button {
    left: unset;
    right: -15px;
    top: 50%;
    bottom: unset;
    margin-top: -7.5px;
    transform: rotate(-90deg);
  }
}

@media screen and (max-width: 767px) {
  .layout-item {
    grid-template-columns: minmax(0, 1fr);
    gap: 15px;
  }
  .layout-item__content {
    margin-top: 15px;
  }
  .layout-item__img {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .layout-slider__item {
    width: 300px;
  }
  .layout-category__nav {
    align-items: flex-start;
    flex-direction: column;
  }
  .layout-category__item + .layout-category__item {
    margin-top: 10px;
  }
}

.section-hero__wrapper {
  height: calc(100vh - 130px);
  min-height: 700px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-hero__wrapper::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  display: block;
  background-color: rgba(39, 37, 37, 0.3);
  z-index: 2;
}

.hero-bg-slider {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.hero-bg-slider__item {
  height: 100%;
  width: 100%;
}

.hero-bg-slider__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-slider {
  position: relative;
  z-index: 3;
}

.hero-slider__item {
  width: 507px;
  border: solid 1px #fff;
  padding: 33px 0 25px 0;
}

.hero-slider__item-status {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 25px;
  font-weight: 700;
  text-transform: uppercase;
  color: #be983e;
}

.hero-slider__item-status::before, .hero-slider__item-status::after {
  content: '';
  width: 160px;
  height: 1px;
  display: block;
  background-color: #be983e;
}

.hero-slider__item-title {
  margin-top: 30px;
  font-size: 30px;
  font-weight: 700;
  color: #fff;
  text-align: center;
}

.hero-slider__item-address {
  font-size: 20px;
  color: #fff;
  text-align: center;
}

.hero-slider__img {
  margin-top: 20px;
  position: relative;
  width: auto;
  height: 275px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-slider__img-item {
  position: absolute;
  top: 0;
  height: 100%;
}

.hero-slider__ready {
  margin-top: 20px;
}

.hero-slider__ready-row {
  position: relative;
  max-width: 387px;
  height: 15px;
  width: 100%;
  margin: 0 auto;
  background-color: rgba(190, 152, 62, 0.55);
}

.hero-slider__ready-completed {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-image: linear-gradient(to top, #be983e, #dfbe5f, #be983e);
}

.hero-slider__ready-text {
  margin-top: 20px;
  text-align: center;
  font-size: 16px;
  color: #fff;
}

.hero-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 35px;
}

.hero-pagination .swiper-pagination-bullet {
  opacity: 1;
  background-color: rgba(248, 248, 248, 0.5);
}

.hero-pagination .swiper-pagination-bullet-active {
  background-color: #f8f8f8;
}

@media screen and (max-width: 1440px) {
  .section-hero__wrapper {
    height: auto;
    padding: 30px 0;
  }
}

@media screen and (max-width: 650px) {
  .hero-slider__item {
    width: 90%;
  }
  .hero-slider__item-status::before, .hero-slider__item-status::after {
    width: 25%;
  }
  .hero-slider__ready {
    padding: 0 15px;
  }
}

.tabs-content {
  height: 100%;
}

.tabs-content__item {
  height: 100%;
  display: none;
}

.tabs-content__item.active {
  display: block;
}

.tabs-nav {
  position: relative;
}
