.header{
  &__wrapper{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

    padding: 6px 0 12px 40px;

    height: 130px;
  }
  &__block{
    padding-bottom: 20px;
    &-logo{
      padding-bottom: 0;
    }
    &-burger{
      display: none;
    }
  }
  &__logo{
    height: 110px;

    display: flex;
    align-items: center;
    justify-content: center;
    &-img{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__link{
    font-size: 12px;
    color:#575756;

    text-transform: uppercase;
  }
  &__phone{
    font-size: 18px;
    font-weight: 700;
    color:#575756;

    text-transform: uppercase;
  }
  &-nav{
    &__list{
      display: flex;
      align-items: center;
    }
    &__item + &__item{
      margin-left: 40px;
    }
  }
  &-social{
    &__wrapper{
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
    &__item + &__item{
      margin-left: 15px;
    }
    &__img{
      width: 25px;
      height: 20px;

      object-fit: contain;
    }
  }
  &-phone{
    margin-top: 20px;

    text-align: right;
  }
}

.burger{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;

  width: 30px;
  height: 20px;

  position: relative;
  &__item{
    width: 100%;
    height: 2px;
    display: block;

    flex-shrink: 0;

    background-color: #000000;

    border-radius: 2px;

    transition: 0.5s all;
  }
  &.active{
    .burger{
      &__item{
        &:nth-child(2){
          display: none;
        }
        &:nth-child(1){
          position: absolute;
          left: 0;
          top: 0;

          transform: rotate(45deg);
        }
        &:nth-child(3){
          position: absolute;
          right: 0;
          top: 0;

          transform: rotate(-45deg);
        }
      }
    }
  }
}

.nav{
  &-item{
    &__link{
      &_mobile{
        display: none;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .nav{
    &-item{
      &__link{
        &_mobile{
          display: block;
        }
      }
    }
  }
  .header{
    &__block{
      display: none;
      padding-bottom: 0;
      flex:33%;
      &-burger{
        display: block;
        z-index: 6;
      }
      &-logo{
        display: block;
      }
      &-social{
        display: block;
      }
      &-nav{
        position: fixed;
        top: 0;
        left: 0;
        width: 50%;
        height: 100vh;
        background-color: #fff;
        display: block;
        z-index: 5;
        padding-top: 80px;

        transform: translateX(-100%);
        opacity: 0;

        transition: 0.5s all linear;
        &.active{
          opacity: 1;
          transform: translateX(0);
        }
      }
    }
    &__wrapper{
      align-items: center;
      justify-content: space-between;

      padding: 6px 0;
      height: auto;
    }
    &__logo{
      height: 40px;
    }
    &-phone{
      margin-top: 5px;
    }
    &-nav{
      &__list{
        flex-direction: column;
        align-items: flex-start;
        padding-left: 15px;
      }
      &__item + &__item{
        margin-left: 0;
        margin-top: 10px;
      }
    }
  }
}

@media screen and (max-width: 375px) {
  .header{
    &__phone{
      font-size: 10px;
    }
    &-phone{
      margin-top: 0;
    }
    &-social{
      &__img{
        width: 15px;
        height: 15px;
      }
    }
  }
}