.footer{
  &__wrapper{
    background-color: #363636;

    padding: 5px 0 30px 0;
  }
  &__row{
    display: flex;
    align-items: flex-start;
  }
  &__row + &__row{
    margin-top: 10px;
  }
  &__social{
    margin-left: auto;

    align-self: center;
  }
  &__phone{
    margin-left: 45px;
    align-self: center;
    &-label{
      font-size: 18px;
      font-weight: 700;

      color:#ffffff;
    }
  }
  &__copyright{
    &-label{
      font-size: 12px;
      line-height: 22px;

      color:#fff;
    }
  }
  &__nav{
    margin-left: auto;
  }
  &-nav{
    &__list{
      display: flex;
      align-items: center;
    }
    &__item + &__item{
      margin-left: 35px;
    }
    &__link{
      font-size: 12px;
      text-transform: uppercase;
      color:#ffffff;

      transition: .3s color;
      &:hover{
        color:#be983e;
      }
    }
  }
  &-social{
    display: flex;
    align-items: center;
    justify-content: center;
    &__item{
      width: 25px;
      height: 16px;
    }
    &__link{
      display: flex;
      align-items: center;
      justify-content: center;

      width: 100%;
      height: 100%;
      svg{
        width: 100%;
        height: 100%;

        fill: #fff;
      }
      path{
        fill:#fff;

        transition: .3s fill;
      }
      &:hover{
        path{
          fill: #be983e;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .footer{
    &__row{
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    &__social,&__phone{
      margin-left: 0;
    }
    &__phone{
      margin-top: 10px;
    }
    &__copyright{
      &-label{
        text-align: center;
      }
    }
    &-nav{
      &__list{
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-evenly;
      }
      &__item + &__item{
        margin: 0;
      }
      &__item{
        margin: 5px 10px;
      }
    }
  }
}