.section{
  &__wrapper{
    padding: 75px 0;
  }
  &__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 24.5px;
    font-weight: 700;

    color:#262626;
  }
  &__desc{
    font-size: 14px;
    line-height: 19px;

    color:#262626;

    text-align: justify;
    p + p{
      margin-top: 15px;
    }
  }
  &__link{
    font-size: 14px;
    color:#262626;
    text-decoration: underline;
    text-underline-position: under;
    text-decoration-color: #be983e;
  }
  &__header + &__content{
    margin-top: 44px;
  }
}

@media screen and (max-width: 991px) {
  .section{
    &__wrapper{
      padding: 30px 0;
    }
  }
}

@media screen and (max-width: 475px) {
  .section{
    &__title{
      font-size: 18px;
    }
    &__header + &__content{
      margin-top: 15px;
    }
  }
}