.news{
  &-slide{
    width: calc(25% - 25px);
    &_big{
      width: calc(50% - 50px);
    }
  }
  &-item{
    &__img{
      height: 368px;
      &-item{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__content{
      margin-top: 25px;
    }
    &__time{
      font-size: 14px;
      font-weight: 500;
      color:#262626;
      &-label{
        font-size: 24px;
        font-weight: 700;
        color:#be983e;

        margin-right: 5px;
      }
    }
    &__title{
      margin-top: 20px;
      &-label{
        font-size: 18px;
        font-weight: 700;
        color:#262626;
      }
    }
    &__desc{
      margin-top: 30px;

      font-size: 14px;
      line-height: 19px;
      color:#262626;
    }
    &__footer{
      margin-top: 15px;
    }
    &__link{
      font-size: 12px;
      color:#848484;

      text-decoration: underline;
      text-underline-position: under;

      transition: .3s color, .3s text-decoration;
      &:hover{
        color:#be983e;

        text-decoration-color: transparent;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .news{
    &-slide{
      width: 100%;
      &_big{
        width: 100%;
      }
    }
  }
}